.order {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
}

.order__title {
  color: #f2f2f3;
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.order__image {
  width: 102px;
  height: 107px;
}
